// - —ç—Ç–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å –£–ù–ò–í–ï–†–°–ê–õ–¨–ù–´–ú–ò –∏ –∏–º–µ—Ç—å –∫—Ä–∞—Å–∏–≤—ã–µ —Å—Ç–∏–ª–∏ (—Å–æ–∑–¥–∞—Ç—å —Å–∞–º–æ–º—É –∏–ª–∏ –Ω–∞–π—Ç–∏ –≤ –∏–Ω—Ç–µ—Ä–Ω–µ—Ç–µ)
// - –¥–æ–±–∞–≤—å—Ç–µ –≤ –ø—Ä–æ–µ–∫—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É, –≤ –∫–æ—Ç–æ—Ä–æ–π –æ—Ç—Ä–∏—Å—É–π—Ç–µ —ç—Ç–∏ —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –¥–ª—è –¥–µ–º–æ–Ω—Å—Ç—Ä–∞—Ü–∏–∏
// - –∑–∞–º–µ–Ω–∏—Ç–µ –≤ –ø—Ä–µ–¥—ã–¥—É—â–∏—Ö –¥–æ–º–∞—à–∫–∞—Ö —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–µ –∫–Ω–æ–ø–∫–∏ –∏ –∏–Ω–ø—É—Ç—ã –≤–∞—à–∏–º–∏ —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–º–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞–º–∏, –≤ –ø–æ—Å–ª–µ–¥—É—é—â–∏—Ö –¥–æ–º–∞—à–∫–∞—Ö –∏—Å–ø–æ–ª—å–∑—É–π—Ç–µ —Å–≤–æ–∏ —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –≤–º–µ—Å—Ç–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã—Ö
// –∑–∞–¥–∞—á–∏ —Å–æ –∑–≤—ë–∑–¥–æ—á–∫–∞–º–∏:
// * —Å–¥–µ–ª–∞–π—Ç–µ —Ç–∞–∫ —á—Ç–æ–± onChange –≤–∞—à–∏—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç —Å—Ä–∞–∑—É –¥–∞–≤–∞–ª –Ω—É–∂–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ (value/checked), –∞ –Ω–µ –æ–±—ä–µ–∫—Ç –∏–≤–µ–Ω—Ç–∞
// * –¥–æ–±–∞–≤—å—Ç–µ –≤ –∏–Ω–ø—É—Ç—É –ø—Ä–æ–ø—Å –¥–ª—è —Ñ—É–Ω–∫—Ü–∏–∏, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –≤—ã–ø–æ–ª–Ω—è—Ç—å—Å—è –ø–æ –Ω–∞–∂–∞—Ç–∏—é –∫–Ω–æ–ø–∫–∏ Enter
// * –¥–æ–±–∞–≤—å—Ç–µ –≤ —Å–≤–æ—é –∫–Ω–æ–ø–∫—É –Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π –ø—Ä–æ–ø—Å –¥–ª—è –≤—ã–±–æ—Ä–∞ —Ç–∏–ø–∞ –∫–Ω–æ–ø–∫–∏: —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π (–≤—ã–±—Ä–∞–Ω –∏–∑–Ω–∞—á–∞–ª—å–Ω–æ) –∏–ª–∏ –∫—Ä–∞—Å–Ω—ã–π (–¥–ª—è –∫–Ω–æ–ø–æ–∫ —É–¥–∞–ª–µ–Ω–∏—è/–æ—Ç–º–µ–Ω—ã/–≤—ã—Ö–æ–¥–∞/...), —Å—Ç–∏–ª–∏ –¥–ª—è —Ä–∞–∑–Ω—ã—Ö —Ç–∏–ø–æ–≤ –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å —Ä–∞–∑–Ω—ã–µ
// * –¥–æ–±–∞–≤–∏—Ç—å –Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π –ø—Ä–æ–ø—Å error –≤ –∏–Ω–ø—É—Ç, –≤ —Å–ª—É—á–∞–µ –ø–µ—Ä–µ–¥–∞—á–∏ –≤ –Ω–µ–≥–æ –∑–Ω–∞—á–µ–Ω–∏—è –Ω—É–∂–Ω–æ –º–µ–Ω—è—Ç—å —Å—Ç–∏–ª—å –∏–Ω–ø—É—Ç–∞ –Ω–∞ –∫—Ä–∞—Å–Ω—ã–π


type PropsInputType =
    DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>
    & {
        error?: string|null,
        setError?: (value: string | null) => void,
        onChangeItem?: (value: string) => void,
        addItem: () => void
    };


const InputForm = (props: PropsInputType) => {

    const { error, addItem, setError, onChangeItem, ...restProps } = props;

    function onChange(e: ChangeEvent<HTMLInputElement>) {
        if (setError && e.currentTarget.value.trim()) { setError(null) }
        onChangeItem && onChangeItem(e.currentTarget.value)
    };
 
    function onEnterKeyPress(e: KeyboardEvent<HTMLInputElement>) {
        if (e.charCode === 13) { addItem && addItem() };
    };
    
    function onBlur() {setError && setError(null) };


    return (
        <input className={error ? `${style.input} ${style.error}` : 'input'}
            type="text"
            onBlur={onBlur}
            onChange={onChange}
            onKeyPress={onEnterKeyPress}
            {...restProps}
        />
    )
};
export default InputForm;
