        message: 
        '–°—É—â–µ—Å—Ç–≤—É–µ—Ç —Ç–µ–æ—Ä–∏—è, –∫–æ—Ç–æ—Ä–∞—è —É—Ç–≤–µ—Ä–∂–¥–∞–µ—Ç, —á—Ç–æ –µ—Å–ª–∏ –æ–¥–Ω–∞–∂–¥—ã –∫—Ç–æ-–Ω–∏–±—É–¥—å –¥–æ–ø–æ–¥–ª–∏–Ω–Ω–æ –≤—ã—è—Å–Ω–∏—Ç, —á—Ç–æ —Ç–∞–∫–æ–µ –∏ –¥–ª—è —á–µ–≥–æ –Ω—É–∂–Ω–∞ –í—Å–µ–ª–µ–Ω–Ω–∞—è, –æ–Ω–∞ —Ç–æ—Ç—á–∞—Å –∂–µ –∏—Å—á–µ–∑–Ω–µ—Ç, –∏ –≤–º–µ—Å—Ç–æ –Ω–µ–µ –ø–æ—è–≤–∏—Ç—Å—è –Ω–µ—á—Ç–æ –µ—â–µ –±–æ–ª–µ–µ –ø—Ä–∏—á—É–¥–ª–∏–≤–æ–µ –∏ –Ω–µ–æ–±—ä—è—Å–Ω–∏–º–æ–µ. –°—É—â–µ—Å—Ç–≤—É–µ—Ç –∏ –¥—Ä—É–≥–∞—è —Ç–µ–æ—Ä–∏—è, —Å–æ–≥–ª–∞—Å–Ω–æ –∫–æ—Ç–æ—Ä–æ–π —ç—Ç–æ —É–∂–µ —Å–ª—É—á–∏–ª–æ—Å—å.',
